.button {
  all: unset;
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 30px;
  right: 10px;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #ffaa44;
  font-size: 2em;
  font-weight: 800;
  color: #fff;
  cursor: pointer;
}
