@import url("https://fonts.googleapis.com/css2?family=Andada+Pro:wght@700&display=swap");
.logo {
  font-family: "Andada Pro", serif;
  font-weight: 700;
  font-size: 1.2em;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.logo a {
  all: unset;
  cursor: pointer;
}

.logo::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -14px;
  width: 60%;
  height: 8px;
  background-color: rgba(226, 132, 19, 0.48);
  border-radius: 15px;
}

@media screen and (max-width: 700px) {
  .logo {
    font-size: 1em;
  }
  .logo::after {
    bottom: -10px;
    height: 6px;
  }
}
